<template>
  <div style="background-color: #fff">
    <dropDown
      sign="trainDropDown"
      ref="dropDown"
      :options="options"
      @change="changeDropDown"
      @open="openDrop"
    >
      <view v-show="selectType == 'belong_place_id'">
        <dropDownCitySelect @confirm="citySelectConfirm" @reset="citySelectReset" />
      </view>
      <view v-show="selectType == 'tag_ids'">
        <dropDownProjectSelect @confirm="projectSelectConfirm" @reset="projectSelectReset" />
      </view>
    </dropDown>
  </div>
</template>

<script>
import dropDown from '@/components/dropDown/index.vue'
import dropDownCitySelect from '@/components/dropDown/dropDownCitySelect.vue'
import dropDownProjectSelect from '@/components/dropDown/dropDownProjectSelect.vue'

export default {
  components: {
    dropDown,
    dropDownCitySelect,
    dropDownProjectSelect,
  },
  data() {
    return {
      searchFormValue: {},
      selectType: '',
      options: {
        belong_place_id: {
          label: '全部区域',
          value: 0,
          child: [],
          useSlot: true,
        },
        tag_ids: {
          label: '全部项目',
          value: 0,
          child: [],
          useSlot: true,
        },
        status: {
          label: '全部状态',
          value: 0,
          activeIndex: 0,
          child: [
            {
              label: '全部状态',
              value: 0,
            },
            {
              label: '待开始',
              value: 1,
            },
            {
              label: '报名中',
              value: 2,
            },
            {
              label: '报名结束',
              value: 3,
            },
          ],
        },
      },
    }
  },
  onLoad(options) {},
  methods: {
    init() {
      this.$refs['dropDown'].init()
    },
    openDrop(e) {
      this.selectType = e.name
      this.$emit('open', e)
    },
    close() {
      this.$refs['dropDown'].close()
    },
    changeDropDown(res) {
      let obj = {}
      res.map((e) => {
        obj[e.name] = e.value
      })
      this.searchFormValue = {
        ...this.searchFormValue,
        ...obj,
      }
      this.handleSearch()
    },
    //  城市选择确认
    citySelectConfirm(e) {
      let item = e.data[e.data.length - 1]
      let values = e.values
      this.options['belong_place_id'].label = item ? item.name : '全部区域'
      this.searchFormValue['company_area_id'] = values.length ? values[values.length - 1] : ''
      this.$refs['dropDown'].close()
      this.handleSearch()
    },
    // 城市选择重置
    citySelectReset() {
      this.options['belong_place_id'].label = '全部区域'
      this.searchFormValue['company_area_id'] = ''
      this.$refs['dropDown'].close()
      this.handleSearch()
    },
    // 项目选择确认
    projectSelectConfirm(e) {
      let item = e.data[e.data.length - 1]
      let values = e.values
      this.options['tag_ids'].label = item ? item.name : '全部项目'
      this.searchFormValue['tag_ids'] = values.length ? values[values.length - 1] : ''
      this.$refs['dropDown'].close()
      this.handleSearch()
    },
    // 项目选择重置
    projectSelectReset() {
      this.options['tag_ids'].label = '全部项目'
      this.searchFormValue['tag_ids'] = ''
      this.$refs['dropDown'].close()
      this.handleSearch()
    },
    handleSearch() {
      console.log(this.searchFormValue)
      let params = JSON.parse(JSON.stringify(this.searchFormValue))
      for (const key in params) {
        const item = params[key]
        if (item == '') {
          delete params[key]
        }
      }
      this.$emit('search', params)
    },
  },
}
</script>

<style scoped lang="scss"></style>
